.storeCardSection {
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
  width: 900px;
  margin: 0 auto;
  height: 200px;
  overflow: hidden;
  white-space: nowrap; }

.width {
  width: 120px;
  position: relative;
  margin: 0 auto;
  height: 500px;
  overflow: hidden;
  white-space: nowrap; }

.slider-wrapper {
  position: relative;
  height: 100%;
  width: 100%; }

.arrow {
  height: 50px;
  width: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: rgba(255, 157, 0, 0.448);
  border-radius: 50%;
  cursor: pointer;
  transition: -webkit-transform ease-in 0.1s;
  transition: transform ease-in 0.1s;
  transition: transform ease-in 0.1s, -webkit-transform ease-in 0.1s; }
  .arrow:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    box-shadow: 0px 5px 18px 0px rgba(0, 0, 0, 0.3); }

.nextArrow {
  position: absolute;
  top: 30%;
  right: -20px;
  z-index: 999;
  color: #d1caca; }

.backArrow {
  position: absolute;
  top: 30%;
  left: -20px;
  z-index: 999; }

.title {
  font-family: serif;
  font-size: 36px;
  font-weight: 400;
  font-style: italic;
  line-height: 44px;
  margin: 0 0 12px;
  text-align: center; }

.btnhvr {
  cursor: pointer;
  transition: -webkit-transform ease-in 0.1s;
  transition: transform ease-in 0.1s;
  transition: transform ease-in 0.1s, -webkit-transform ease-in 0.1s; }
  .btnhvr:hover {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
    box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.3); }

.hiding {
  transition: -webkit-transform ease-out 2s;
  transition: transform ease-out 2s;
  transition: transform ease-out 2s, -webkit-transform ease-out 2s; }

.subtitle {
